import { TRPCRouterRecord } from "@trpc/server";
import { z } from "zod";

import { eq } from "@acme/db";
import { VideoComment } from "@acme/db/schema";

import { protectedProcedure } from "../trpc";

export const commentRouter = {
  byVideoId: protectedProcedure
    .input(
      z.object({
        videoId: z.string(),
      }),
    )
    .query(async ({ ctx, input }) => {
      const comments = await ctx.db.query.VideoComment.findMany({
        where: eq(VideoComment.videoId, input.videoId),
        orderBy: (videoComment, { desc }) => [desc(videoComment.createdAt)],
        with: {
          user: {
            columns: {
              id: true,
              name: true,
              image: true,
            },
          },
        },
      });

      return comments;
    }),

  create: protectedProcedure
    .input(
      z.object({
        videoId: z.string(),
        content: z.string(),
        startTime: z.number().optional(),
        endTime: z.number().optional(),
      }),
    )
    .mutation(async ({ ctx, input }) => {
      return ctx.db
        .insert(VideoComment)
        .values({
          videoId: input.videoId,
          content: input.content,
          userId: ctx.session.user.id,
          startTime: input.startTime,
          endTime: input.endTime,
        })
        .returning({ id: VideoComment.id });
    }),

  delete: protectedProcedure
    .input(z.string())
    .mutation(async ({ ctx, input }) => {
      return ctx.db.delete(VideoComment).where(eq(VideoComment.id, input));
    }),
} satisfies TRPCRouterRecord;
