import type { TRPCRouterRecord } from "@trpc/server";
import {
  AbortMultipartUploadCommand,
  CompleteMultipartUploadCommand,
  CreateMultipartUploadCommand,
  PutObjectCommand,
  S3Client,
  UploadPartCommand,
} from "@aws-sdk/client-s3";
import { getSignedUrl } from "@aws-sdk/s3-request-presigner";
import { TRPCError } from "@trpc/server";
import { z } from "zod";

import { protectedProcedure } from "../trpc";

const bucketName = process.env.AWS_BUCKET_NAME;
const maxMB = 700;

export const awsRouter = {
  initiateMultipartUpload: protectedProcedure
    .input(
      z.object({
        videoId: z.string(),
        parts: z.number(),
        contentLength: z.number(),
      }),
    )
    .mutation(async ({ ctx, input }) => {
      const key = input.videoId;

      if (input.contentLength > maxMB * 1024 * 1024) {
        throw new TRPCError({
          code: "PRECONDITION_FAILED",
          message: "File is too large",
        });
      }

      const client = new S3Client({
        useAccelerateEndpoint: true,
      });

      const createCommand = new CreateMultipartUploadCommand({
        Bucket: bucketName,
        Key: key,
        Metadata: {
          videoid: input.videoId,
          userid: ctx.session.user.id,
        },
      });

      const { UploadId } = await client.send(createCommand);
      if (!UploadId) throw new Error("Failed to initiate multipart upload");

      const signedUrls = await Promise.all(
        Array.from({ length: input.parts }, async (_, index) => {
          const uploadPartCommand = new UploadPartCommand({
            Bucket: bucketName,
            Key: key,
            UploadId,
            PartNumber: index + 1,
          });

          const signedUrl = await getSignedUrl(client, uploadPartCommand, {
            expiresIn: 3600,
          });
          return { url: signedUrl, partNumber: index + 1 };
        }),
      );

      return {
        uploadId: UploadId,
        signedUrls,
      };
    }),
  completeMultipartUpload: protectedProcedure
    .input(
      z.object({
        videoId: z.string(),
        uploadId: z.string(),
        parts: z.array(
          z.object({
            ETag: z.string(),
            PartNumber: z.number(),
          }),
        ),
      }),
    )
    .mutation(async ({ ctx, input }) => {
      const key = input.videoId;

      const client = new S3Client({
        useAccelerateEndpoint: true,
      });

      try {
        const command = new CompleteMultipartUploadCommand({
          Bucket: bucketName,
          Key: key,
          UploadId: input.uploadId,
          MultipartUpload: {
            Parts: input.parts,
          },
        });

        await client.send(command);

        const videoUrl = `https://${bucketName}.s3.amazonaws.com/${key}`;

        return { success: true, videoUrl };
      } catch (_error) {
        await client.send(
          new AbortMultipartUploadCommand({
            Bucket: bucketName,
            Key: key,
            UploadId: input.uploadId,
          }),
        );
        // Sentry.captureException(_error);

        throw new TRPCError({
          code: "INTERNAL_SERVER_ERROR",
          message: "Failed to complete multipart upload",
        });
      }
    }),
} satisfies TRPCRouterRecord;
